import React from 'react'
import Arrow from '../arrow.png'
import Typing from 'react-typing-animation'
import ScrollAnimation from 'react-animate-on-scroll';
import phone from '../phone.svg'
import map from '../map.svg'
import facebook from '../facebook.svg'
import gmail from '../gmail.svg'
import yahoo from '../yahoo.svg'
import resume from '../demo.docx'

function Home(){
    
    let paddingTop = {
        paddingTop : '70px'
    }
    
    let arrow = {
        height : '100px',
        marginTop : '100px' ,
        cursor : 'pointer',
        marginBottom : '60px'
        
    }
    let php ={
        width : '60%'
    }
    let mysql ={
        width : '50%'
    }
    let c ={
        width : '10%'
    }
    
    let secondSection = {
        height : '100%',
        background : 'linear-gradient(to bottom , #f1f1f1 , white)',
        
        
        
        
    }
    
    let skill = {
        paddingTop : '70px',
        paddingBottom : '70px'
    }
    
    let thirdContainer = {
        height : '100%' ,
        boxShadow : '5px 5px 15px 5px lightgray ',
        color : '#f1f1f1' ,
        background : '#28a745'
        
    }
    let icon = {
        width : '50px' ,
       
        paddingRight : '10px' ,
        paddingBottom : '10px'
        
    }
    function contact(){
        window.scrollTo({
            top : 3000 ,
            behavior : 'smooth'
        })
    }
    function slide(){
        window.scrollTo({
            top : 700,
            behavior : 'smooth'
        })
    }
    return(
        <div  >
        
            <section className = 'container text-light pt-5 '>
            <Typing ><h1  style = {paddingTop} id='name'>Hi im John Paul Torrecarion</h1>

        </Typing>
        
        <Typing>
            
             <Typing.Delay ms={4000} />
            <h4>Backend / Application Developer</h4>
            <Typing.Backspace count={35} />
          </Typing>
        <p className = 'pt-4' id='para'>Hi welcome to my site im John Paul, I am graduate of Bachelor of Science in Information Technology, I am passionate about backend development and computer troubleshooting.</p>
        <section id='my-btn'>
        <a className='btn btn-dark' href = {resume}>Download resume</a> <button className='btn btn-custom' onClick = {contact}>Contact me</button>
        </section>
        <div className ='text-center'>
        <img className='arrow'  src = { Arrow } style = { arrow } alt='arrow' onClick = { slide }/>
        </div>
            </section>
        
        
        
        
        <section style = { secondSection } className='pt-5 ' >
        <div className='container'>
        
        <div className='row' style = {skill}>
        
            <section className='col-md-6'>
        <ScrollAnimation animateIn="fadeIn" >
            <div className="card  ">
              <div className="card-body">
                <h4 className="card-title">PHP</h4>
                <p className="card-text">
                   Hypertext Preprocessor (or simply PHP) is a server-side scripting language designed for Web development.
                </p>
                <div className="progress"> <div className="progress-bar progress-bar-striped bg-danger" role="progressbar" style={ php } aria-valuenow="50" aria-valuemin="0" aria-valuemax="100"></div> </div>
              </div>
            </div>
        
             <div className="card  ">
              <div className="card-body ">
                <h4 className="card-title">C++</h4>
                <p className="card-text">
                  C++ is a statically-typed, free-form, (usually) compiled, multi-paradigm, intermediate-level general-purpose middle-level programming language.‚Äù
                </p>
                <div className="progress"> <div className="progress-bar progress-bar-striped bg-info" role="progressbar" style={ c } aria-valuenow="50" aria-valuemin="0" aria-valuemax="100"></div> </div>
              </div>
            </div>
        
            <div className="card ">
              <div className="card-body">
                <h4 className="card-title">MySql</h4>
                <p className="card-text">
                  SQL stands for Structured Query Language, and it is a programming language designed for querying data from a database. MySQL is a relational database management system, which is a completely different thing.
                </p>
                <div className="progress"> <div className="progress-bar progress-bar-striped bg-success" role="progressbar" style={ mysql } aria-valuenow="50" aria-valuemin="0" aria-valuemax="100"></div> </div>
              </div>
           
            </div>
        </ScrollAnimation>
        </section>
        
        <section className='col-md-6' id='skill_desc'>
        <ScrollAnimation animateIn="fadeIn">
        <h1 className='text-success' id='quem'> Services</h1>
        <p>I have basic skills about network and hardware troubleshooting. I also know about backend development using PHP and MySql which i have exceled in my past 4 years being an Information Technology student.</p>
        </ScrollAnimation>
        </section>
           
        
            
        </div>
        
        </div>
        </section>
        
        <section style = { thirdContainer } >
            <div className='container pt-5 '>
            <div className='col-md-6'>
            <img src = {map } style = {icon} className ='float-left' alt = 'img'/><span><h3 >Contact and location</h3></span><br/>
        <p>#24 3rd West Crame San Juan City </p>
            <img  src = {phone} style = {icon} alt ='img'/><span>   (+639) 977-534-600 </span>
            </div>
            <br/>
            <br/>
            <br/>
        <div className='col-md-6'>
            <h3>Accounts</h3>
        <br/>
            <img  src = {phone} style = {icon} alt ='img'/>
        
        <img  src = {facebook} style = {icon}  alt ='img'/>
        
        <img  src = {gmail} style = {icon} alt ='img'/>
       
        <img  src = {yahoo} style = {icon} alt ='img' />
        
            </div>
            </div>
        </section>
        
        
        </div>
        
    )
}
export default Home