import React from 'react'
import '../styles/AboutStyle.css'
import Particles from 'react-particles-js';
import Printer from '../printer.svg'
import resume from '../demo.docx'


function About(){
    
   let paddingTop = {
       marginTop : '60px' ,
       
       
   }
   
   let pr = {
       width : '50px',
       cursor : 'pointer'
   }
   
   
    
    return(
         
        <div id='myabout'>
            
            <section className = 'text-center text-light demo ' style ={paddingTop} >
                    
            <h1>JOHN PAUL TORRECARION</h1>
                
                <p id='profession' className='mx-auto' >BACKEND / APPLICATION DEVELOPER</p>
       <Particles height = '300px' />
        </section>
       
            <section >
               <div className='container' >
                        <section className='row'>
                                <div className='col-md-2 '> 
                                    <p>ABOUT</p>
                                </div>
        <div className='col-md-8'> 
        <p>I am a graduate of Bachelor of Information Technology at Access Computer College, I am passionate about computer hardware troubleshooting and application development.</p>
                                </div>
        <div className='col-md-2'> 
                                   <p>PRINT RESUME</p>
                                    <a href = {resume} download>
                                    <img src ={Printer} alt='img' style = {pr} />
                                    </a>
        
                                </div>
                        </section>
                </div>
            </section>
        
        </div>
        
    )
}
export default About